import { LOAN_DETAIL_BEGIN, LOAN_DETAIL_FAILURE, LOAN_DETAIL_SUCCESS } from "./1_ActionConstants";

export const LoanDetailBegin = () => ({
  type: LOAN_DETAIL_BEGIN,
});

export const LoanDetailSuccess = (loanObj) => ({
  type: LOAN_DETAIL_SUCCESS,
  payload: loanObj,
});

export const LoanDetailFailure = (error) => ({
  type: LOAN_DETAIL_FAILURE,
  payload: { error },
});

export function updateLoanDetail(loanObj) {
  return (dispatch, getState) => {
    //redux passes dispatch & getState as args into thunk functions
    dispatch(LoanDetailBegin());
    if (loanObj) {
      dispatch(LoanDetailSuccess(loanObj));
      return true;
    } else {
      dispatch(LoanDetailFailure("error"));
      return false;
    }
  };
}
